import React, { useEffect, useState } from "react";
import { Card, Row, Col, Button, Input } from "antd";
import BasicColumn from "../Charts/BarChart";
import dashboardServices from "services/DashboardServices";
import { useSelector } from "react-redux";
import { DASHBOARD_ALLOWED_ROLES } from "constants/CommonConstants";
const { Search } = Input;
const AccountsChart = () => {
  const [activeAccount, setActiveAccount] = useState();
  const [cities, setCities] = useState();
  const [series, setSeries] = useState();
  const [accountList, setAccountList] = useState([]);
  const [localAccountList, setLocalAccountList] = useState([]);
  const role = useSelector(state => state?.myTeams?.userInfo?.role);
  const [accountInfo, setAccountInfo] = useState([]);
  const getAccounts = async () => {
    const resp  = await dashboardServices.getAccountForChar();
    const { accountList, accountInfo } = resp 
    setAccountList(accountList);
    setLocalAccountList(accountList);
    setAccountInfo(accountInfo);
    if (accountList) {
      const activeAccountInitial = accountList[0];
      setActiveAccount(accountList[0]);
      setCities(accountInfo[activeAccountInitial]?.cities);
      setSeries(accountInfo[activeAccountInitial]?.allocations);
    }
  };

  useEffect(() => {
    if(DASHBOARD_ALLOWED_ROLES.includes(role)){
      getAccounts();
    }
  }, []);

  const onAccountClick = key => {
    setActiveAccount(key);
    setCities(accountInfo[key].cities);
    setSeries(accountInfo[key].allocations);
  };
  const onTextChange = key => {
    if (key) setLocalAccountList(accountList.filter(item => item.toLowerCase().includes(key.toLowerCase())));
    else setLocalAccountList(accountList);
  };
  return (
    <Card
      title="Accounts Overview"
      style={{ display: "flex", flexDirection: "column", flex: 1 }}
      bodyStyle={{ flexGrow: 1 }}
    >
      <Search
        className="mb-2"
        placeholder="Search Account"
        onSearch={onTextChange}
        onChange={e => onTextChange(e.target.value)}
      />
      <Row>
        <Col xs={24} sm={24} md={24} lg={12} xxl={5}>
          <div style={{ height: 349, overflow: "auto" }}>
            {localAccountList?.sort()?.map((elm, i) => (
              <div
                key={i}
                className={`d-flex align-items-center justify-content-between mb-2 cursor-pointer`}
                style={{ backgroundColor: activeAccount === elm && "rgba(62, 121, 247, 0.1)" }}
                onClick={() => onAccountClick(elm)}
              >
                <Button type="link">
                  <h5 className="mb-0">{elm}</h5>
                </Button>
              </div>
            ))}
          </div>
        </Col>
        <Col xs={24} sm={24} md={24} lg={12} xxl={13} style={{ margin: "auto" }}>
          <BasicColumn categories={cities} series={series} />
        </Col>
      </Row>
    </Card>
  );
};

export default AccountsChart;
